export default class BaseHandler {
    constructor() {
        this.allData = {};
    }

    /**
     * Sets title.
     *
     * @param {string} data
     * @returns {void}
     */
    setTitle = (data = null) => {
        this.allData.title = data;
    };

    /**
     * Sets city.
     *
     * @param {string} data
     * @returns {void}
     */
    setCity = (data = null) => {
        this.allData.city = data;
    };

    /**
     * Sets publisher.
     *
     * @param {string} data
     * @returns {void}
     */
    setPublisher = (data = null) => {
        this.allData.publisher = data;
    };

    /**
     * Sets year.
     *
     * @param {string} data
     * @returns {void}
     */
    setYear = (data = null) => {
        this.allData.year = data;
    };

    /**
     * Sets month.
     *
     * @param {string} data
     * @return {void}
     */
    setMonth = (data = null) => {
        this.allData.month = data;
    };

    /**
     * Sets day.
     *
     * @param {string} data
     * @return {void}
     */
    setDay = (data = null) => {
        this.allData.day = data;
    };

    /**
     * Sets volume.
     *
     * @param {string} data
     * @returns {void}
     */
    setVolume = (data = null) => {
        this.allData.volume = data;
    };

    /**
     * Sets pages.
     *
     * @param {string} data
     * @returns {void}
     */
    setPages = (data = null) => {
        this.allData.pages = data;
    };

    /**
     * Sets state/province.
     *
     * @param {string} data
     * @returns {void}
     */
    setStateProvince = (data = null) => {
        this.allData.stateProvince = data;
    };

    /**
     * Sets country region.
     *
     * @param {string} data
     * @returns {void}
     */
    setCountryRegion = (data = null) => {
        this.allData.countryRegion = data;
    };

    /**
     * Sets number of volumes.
     *
     * @param {string} data
     * @returns {void}
     */
    setNumberOfVolumes = (data = null) => {
        this.allData.numberOfVolumes = data;
    };

    /**
     * Sets editor.
     *
     * @param {string} data
     * @return {void}
     */
    setEditor = (data = null) => {
        this.allData.editor = data;
    };

    /**
     * Sets translator.
     *
     * @param {string} data
     * @return {void}
     */
    setTranslator = (data = null) => {
        this.allData.translator = data;
    };

    /**
     * Sets edition.
     *
     * @param {string} data
     * @return {void}
     */
    setEdition = (data = null) => {
        this.allData.edition = data;
    };

    /**
     * Sets comments.
     *
     * @param {string} data
     * @return {void}
     */
    setComments = (data = null) => {
        this.allData.comments = data;
    };

    /**
     * Sets URL.
     *
     * @param {string} data
     * @returns {void}
     */
    setUrl = (data = null) => {
        this.allData.url = data;
    };

    /**
     * Sets production company.
     *
     * @param {string} data
     * @returns {void}
     */
    setProductionCompany = (data = null) => {
        this.allData.productionCompany = data;
    };

    /**
     * Sets year accessed.
     *
     * @param {string} data
     * @returns {void}
     */
    setYearAccessed = (data = null) => {
        this.allData.yearAccessed = data;
    };

    /**
     * Sets month accessed.
     *
     * @param {string} data
     * @returns {void}
     */
    setMonthAccessed = (data = null) => {
        this.allData.monthAccessed = data;
    };

    /**
     * Sets day accessed.
     *
     * @param {string} data
     * @returns {void}
     */
    setDayAccessed = (data) => {
        this.allData.dayAccessed = data;
    };
}
